/*article content*/

h2.title {
    color: #453793;
    font-weight: 400;
    font-size: 22pt;
}

.article-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top:30px;
}

.article-content {
    width: 70%;
}
    .article-content img {
        max-width: 95%;
        height: auto;
    }

.publication-date {
    font-size: .7em;
    font-weight: lighter;
    color: lightslategrey;
    padding-top: 5px;
    padding-bottom: 5px;
}

/*youtube*/
.youtube-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%; /* Aspect ratio for 16:9 videos */
    overflow: hidden;
    margin-bottom: 1px;
    margin-top: 50px;
  }

    .youtube-container iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 90%;
        height: 90%;
        border: 2px solid #333;
        border-radius: 5px;
    }

    /*questions*/

.option-container label {
    margin-left: 5px; /* Adjust the size as needed */
}

#WhatYouLearned {
    margin-top: 30px;
    width:100%;
}

#ImproveMedExHeader {
    display: flex;
    align-items: center;
    justify-content: center;
}

#ImproveMedExIcon {
    margin-right: 10px;
}
.questions {
    width: 99%;
    float: left;
    border: 2px solid #009489;
    border-radius: 10px; /* Add rounded corners */
    border-top-width: 3px;
    /*    align-items: center;*/
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: left;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.1); /* Add box shadow */
}
.questionText{
    font-weight:600;
   /* margin-bottom:10px;*/
}
.questions-content {
    width: 100%;
    
}
.em {
    font-weight: bold;
}
.correct-option {
    color: green;
    
}
.incorrect-option {
    color: red;
}
.unknown-option {
    color: gray;
}
.option-container {
    margin-bottom: 10px;
    padding-left: 10px;
}


/*authors*/
.authors-container {
    width: 25%;
    flex-direction: column;
    padding: 20px;
    margin-left: auto;
}

.authors {
    width: 99%;
    border: 2px solid #009489; 
    border-radius: 10px; /* Add rounded corners */
    border-top-width: 3px;
    align-items: center;
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.1); /* Add box shadow */
}

.author-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
}
.authors-header {
    display: flex;
    justify-content: center;
    color: #13222b; /* Changed color to a blue shade */
    font-weight: 700; /* Made the text bolder */
    font-size: 1.2em; /* Increased the font size */
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5); /* Added text shadow */
}
.author-name, .author-affiliations {
    font-size: .8em;
    justify-content: center;
    align-items: center;
}

.author-image {
    width: 100px;
}
/*pals*/
.pals {
    width: 99%;
    border: 2px solid #009489;
    border-radius: 10px; /* Add rounded corners */
    border-top-width: 3px;
    align-items: center;
    padding: 5px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.1); /* Add box shadow */
    font-size: 11pt;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.pals img{
    width:200px;
}
/*see the science*/

.evidence-modal {
    background-color: white;
    width: 70%;
    max-height: 80%; /* specify max-height */
    position: fixed;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -10%);
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
    outline: 0;
    z-index: 1049;
    padding: 10px;
    margin-top: 30px;
    overflow-y: auto;
}

.modal-content {
    display: flex;
    flex-direction: column;
    align-items: center; /* aligns content to the center vertically */
    justify-content: center; /* aligns content to the center horizontally */
    width: 100%;
}
    .modal-content h2 {
        float: left;
        width: 100%; 
        font-family: "Times New Roman", Times, serif;
    }

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4); /* adjust the 0.4 as needed to get the desired transparency */
    z-index: 0; /* ensure the overlay is below the modal but above everything else */
}
div.es-button {
    align-items: center;
    justify-content: center;
    width: 99%;
    padding: 5px;
    display: flex;
    flex-direction: column;
  }
.evidence-button {
    background: linear-gradient(to bottom, #33cc99 0%, #009966 100%); /* break from the site's primary colors */
    border: none;
    color: white; /* text color */
    font-weight: 600;
    padding: 10px 20px 10px 10px; /* adjust as needed for size */
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px; /* adjust as needed */
    margin: 20px 2px;
    cursor: pointer;
    border-radius: 12px; /* adjust as needed for rounded corners */
    transition: all 0.5s; /* for a smooth hover effect */
}

    .evidence-button:hover {
        background: linear-gradient(to bottom, #009966 0%, #33cc99 100%); /* switch the colors on hover */
        box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.1); /* optional: add a shadow on hover */
    }
:root {
    --beautiful-blue: #13222b; 
}


#esreferences {
    font-size: small;
    text-align: left;
    width:100%;
    padding-left:15px;
    padding-bottom:20px;
  }
    #esreferences ol {
        font-size: small;
        text-align: left;
    }
    #esreferences a {
        font-size: small;
        text-align: left;
    }
.close-button {
    position: absolute;
    right: 10px; /* adjust as needed */
    top: 10px; /* adjust as needed */
    background: none;
    border: none;
    font-size: 1em; /* adjust as needed */
    cursor: pointer;
}


@media (max-width: 768px) {

    /*mobile article content*/

    h2.title {
       font-size: 14pt;
    }

    .article-container {
        flex-direction: column;
        padding-left: 10px;
        padding-right: 10px;
      }
    .article-content {
        width: 99%;
        font-size: 12pt;
        align-items: center;
        justify-content: center;
    }

    /*mobile authors*/

    .authors {
        width: 99%;
        border: 1px solid #3eb54c;
        border-top-width: 4px;
        align-items: center;
        padding: 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
           }
    .author-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 5px;
    }
    .authors-container {
        width: 60%;
        flex-direction: column;
        padding: 10px;
        margin-left: auto;
        margin: 0 auto; 
        justify-content: center;
    }
      
    .author-name, .author-affiliations {
        font-size: small;
       justify-content: center;
        align-items: center;
    }

    /*mobile see the science*/

    .evidence-modal {
        width: 90%;
        max-height: 80%; 
        top: 10%; 
        left: 50%;
        transform: translate(-50%, -10%);
        align-items: flex-start;
        justify-content: flex-start;
        overflow-y: auto;
    }
 
    .evidence-button {
        font-weight: 600;
        padding: 5px 10px 5px 5px;
        font-size: 12px; /* adjust as needed */
        margin: 20px 2px;
        cursor: pointer;
        border-radius: 12px; /* adjust as needed for rounded corners */
        transition: all 0.5s; /* for a smooth hover effect */
    }

    /*mobile youtube and questions*/
    .youtube-container {
         margin-top: 20px;
    }
    #WhatYouLearned {
        margin-top: 10px;
        margin-bottom: 20px;
        }

    /*mobile space above footer*/
    .MuiBox-root {
        padding-top: 0px !important;
    }
    /*mobile pals*/
    .pals {
       font-size: 9pt;
           }
        .pals img {
            max-width:90%;
        }
   }
